.about {
    background-image: url("./assets/bg-about.png");
    background-position: top center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size:cover;
}

.about-grid-container {
    display: grid;
    grid-template-columns: 2fr 5fr;
    column-gap: 40px;
    padding: 32px 128px;
    margin-top: 120px;
}

.profile-pic {
    height: 144px;
    border-radius: 200px;
}

.about-left {
    justify-self: end;
    div {
        margin: 32px 0 20px 0;
        line-height: 1.4;
        h4 {
            margin-bottom: 4px;
        }
    }
}

.about-intro {
    height: 160px;
    display: flex;
    align-items: center;
    line-height: 1.3;
}

.about-section {
    margin-bottom: 40px;
}

.about-list {
    margin: 12px 0 20px 0;
    .title {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-bottom: 2px;
        .company {
            font-weight: 600;
            display: flex;
            align-items: center;
            gap: 4px;
            a {
                display: flex;
                align-items: center;
            }
        }
        .role {
            font-weight: 400;
        }
    }
    .date {
        font-size: 12px;
        margin-bottom: 4px;
    }
    .description {
        font-size: 14px;
        line-height: 1.6;
        color: #7a7a7a;
    }
}